.start-screen {
    text-align: center;
    width: 100%;
}

.start-screen h1 {
    margin-bottom: 20px;
}

.start-screen p {
    margin-bottom: 10px;
}

.article-links {
    display: flex;
    justify-content: center;
    gap: 100px;
    align-items: center;
    margin-bottom: 20px;
}

.article-title-container.fixed-width {
    text-align: center;
    font-size: 3em;
    font-weight: 700;
    color: #457DFF;
    text-decoration: none;
}

.article-title-container.fixed-width .article-link a {
    color: #457DFF;
    text-decoration: underline;
    text-decoration-thickness: 4px;
    text-decoration-color: #113fa3;
  }

.article-link-container {
    text-align: center;
}

.article-link-container a {
    display: block;
    margin-bottom: 10px;
    font-size: 4em;
    font-weight: bold;
}

.start-game-button {
    margin-top: 20px;
}


.article-title-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    width: 50%;
  }
  
  .fixed-width {
    max-width: 100%;
    text-align: center;
  }
  
  .generate-button {
    margin-top: 10px;
  }
  
  .article-links {
    display: flex;
    justify-content: space-between;
  }
  
  .generate-button {
    margin-top: 10px;
    padding: 1px 25px;
    background: linear-gradient(45deg, #457DFF, #345EB8);
    color: #FFFFFF;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 3em;
    transition: transform 0.2s, background 0.3s;
  }
  
  .generate-button:hover {
    transform: scale(1.05);
    background: linear-gradient(45deg, #345EB8, #223C78);
  }

  .start-game-button {
    margin-top: 10px;
    padding: 1px 25px;
    background: linear-gradient(45deg, #457DFF, #345EB8);
    color: #FFFFFF;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    padding: 10px 25px;
    font-size: 1.5em;
    font-weight: 600;
    transition: transform 0.2s, background 0.3s;
    font-family: "League Spartan", sans-serif;
    font-optical-sizing: auto;
  }
  
  .start-game-button:hover {
    transform: scale(1.05);
    background: linear-gradient(45deg, #345EB8, #223C78);
  }